import {
  P_NOTIFY_REQUEST,
  P_NOTIFY_FAIL,
  P_NOTIFY_SUCCESS,
  P_NOTIFY_RESET,
  SPREBOOK_FAIL,
  SPREBOOK_REQUEST,
  SPREBOOK_RESET,
  SPREBOOK_SUCCESS,
} from "../constants/prebookConstants";

export const pnotifyHostReducer = (state = {}, action) => {
  switch (action.type) {
    case P_NOTIFY_REQUEST:
      return { loading: true };
    case P_NOTIFY_SUCCESS:
      return { loading: false, success: true };
    case P_NOTIFY_FAIL:
      return { loading: false, error: action.payload };
    case P_NOTIFY_RESET:
      return {};
    default:
      return state;
  }
};

export const sprebookReducer = (state = {}, action) => {
  switch (action.type) {
    case SPREBOOK_REQUEST:
      return { loading: true };
    case SPREBOOK_SUCCESS:
      return { loading: false, success: true, booked: action.payload.data };
    case SPREBOOK_FAIL:
      return { loading: false, error: action.payload };
    case SPREBOOK_RESET:
      return {};
    default:
      return state;
  }
};
