import React from "react";
import Navigation from "../../components/Navigation";
import styles from "../styles.module.css";
import { Link, useNavigate } from "react-router-dom";
import Slider from "../../components/Slider";
import { FiCheckCircle, FiEdit } from "react-icons/fi";
import { useSelector, useDispatch } from "react-redux";
import { addReturningGuest } from "../../redux/actions/vmsActions";
import {
  Accordion,
  AccordionItem,
  AccordionButton,
  AccordionPanel,
  AccordionIcon,
  Alert,
  AlertIcon,
  Box,
} from "@chakra-ui/react";
import { ADDRETURNING_GUEST_RESET } from "../../redux/constants/vmsConstants";
import Badge from "../../components/Badge";

const RVerifyInfo = () => {
  let navigate = useNavigate();
  const dispatch = useDispatch();
  const returningGuest = useSelector((state) => state.returningGuest);
  const { returning } = returningGuest;
  const user = returning && returning._id;
  const LaptopInfo = JSON.parse(localStorage.getItem("LaptopInfo"));
  const HostInfo = JSON.parse(localStorage.getItem("HostInfo"));
  const photo = localStorage.getItem("photo");
  const { fullname, company, email, mobile } = returning;
  const { laptop, lsn } = LaptopInfo;
  const { host, purpose, appointment } = HostInfo;

  var timeIn = new Date().toLocaleString();
  var date = new Date().toJSON().slice(0, 10).replace(/-/g, "/");

  const submitHandler = (e) => {
    e.preventDefault();

    dispatch(
      addReturningGuest(
        user,
        laptop,
        lsn,
        host,
        purpose,
        appointment,
        photo,
        timeIn,
        date
      )
    );
  };
  const aGuest = useSelector((state) => state.aGuest);
  const { loading, error, success } = aGuest;
  if (success) {
    navigate("/success");
    dispatch({ type: ADDRETURNING_GUEST_RESET });
  }

  return (
    <div>
      <Badge />
      <Navigation />
      <div className={styles.contentx}>
        <div className={styles.formPadding}>
          {loading ? (
            <h3>Submiting Information</h3>
          ) : (
            <>
              <h3>Verify your Information</h3>
              <Slider size="100" />
              <form onSubmit={submitHandler}>
                {error && (
                  <Alert status="error">
                    <AlertIcon />
                    {error}
                  </Alert>
                )}
                <Accordion allowToggle>
                  <AccordionItem>
                    <h2>
                      <AccordionButton className={styles.accordion}>
                        <Box flex="1" textAlign="left">
                          Basic Information
                        </Box>
                        <AccordionIcon />
                      </AccordionButton>
                    </h2>
                    <AccordionPanel pb={4}>
                      <p>
                        <b>Name: </b> {fullname}
                      </p>
                      <p>
                        <b>Company: </b> {company}
                      </p>
                      <p>
                        <b>Email: </b> {email}
                      </p>
                      <p>
                        <b>Mobile: </b> {mobile}
                      </p>
                      <br />
                    </AccordionPanel>
                  </AccordionItem>

                  <AccordionItem>
                    <h2>
                      <AccordionButton className={styles.accordion}>
                        <Box flex="1" textAlign="left">
                          Laptop Information
                        </Box>
                        <AccordionIcon />
                      </AccordionButton>
                    </h2>
                    <AccordionPanel pb={4}>
                      <p>
                        <b>Laptop: </b> {laptop}
                      </p>
                      <p>
                        <b>Laptop SN: </b> {lsn}
                      </p>
                      <br />
                      <Link
                        to="/returningGuest/laptop"
                        className={`${styles.dangerBtn}`}
                      >
                        <FiEdit /> Edit
                      </Link>
                    </AccordionPanel>
                  </AccordionItem>

                  <AccordionItem>
                    <h2>
                      <AccordionButton className={styles.accordion}>
                        <Box flex="1" textAlign="left">
                          Host Information
                        </Box>
                        <AccordionIcon />
                      </AccordionButton>
                    </h2>
                    <AccordionPanel pb={4}>
                      <p>
                        <b>Host: </b> {host}
                      </p>
                      <p>
                        <b>Purpose of Visit: </b> {purpose}
                      </p>
                      <p>
                        <b>Do you have an appointment: </b> {appointment}
                      </p>
                      <p>
                        <b>Date: </b> {date}
                      </p>
                      <p>
                        <b>Time: </b> {timeIn}
                      </p>
                      <br />
                      <Link
                        to="/returningGuest/host"
                        className={`${styles.dangerBtn}`}
                      >
                        <FiEdit /> Edit
                      </Link>
                    </AccordionPanel>
                  </AccordionItem>
                </Accordion>

                <div className={styles.inputContainer}>
                  <button
                    type="submit"
                    className={`${styles.customBtn} ${styles.green}`}
                    style={{ margin: "0 auto" }}
                    value="Next"
                  >
                    <FiCheckCircle /> Submit Information
                  </button>
                </div>
              </form>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default RVerifyInfo;
