import React from 'react'
import styles from './styles.module.css'

const Badge = () => {
    return (
        <div className={styles.badge}>
            <div className={`${styles.smBadge} ${styles.white}`}></div>
            <div className={`${styles.smBadge} ${styles.red}`}></div>
            <div className={`${styles.smBadge} ${styles.blue}`}></div>
            <div className={`${styles.smBadge} ${styles.yellow}`}></div>
            <div className={`${styles.smBadge} ${styles.green}`}></div>
        </div>
    )
}

export default Badge
