import React, { useState, useEffect } from "react";
import Navigation from "../../components/Navigation";
import styles from "../styles.module.css";
import { Link, useNavigate } from "react-router-dom";
import Slider from "../../components/Slider";
import { FiArrowRightCircle, FiArrowLeftCircle } from "react-icons/fi";
import { Alert, AlertIcon, useToast } from "@chakra-ui/react";
import Badge from "../../components/Badge";
import { useSelector } from "react-redux";

const RPlusOne = () => {
  let navigate = useNavigate();
  let toast = useToast();
  const data = JSON.parse(localStorage.getItem("PlusInfo"));
  const returningGuest = useSelector((state) => state.returningGuest);
  const { returning } = returningGuest;
  const [plusOne, setPlusOne] = useState(data ? data.plusOne : "");
  const [question, setQuestion] = useState(data ? data.question : "");
  const [msg, setMsg] = useState(false);

  const questionHandler = (e) => {
    setQuestion(e.target.value);
    if (e.target.value === "No") {
      setPlusOne("None");
    }
  };
  const submitHandler = (e) => {
    e.preventDefault();
    console.log(question, plusOne);
    if (!question || !plusOne) {
      toast({
        title: "Warning",
        description: "All Fields are required",
        status: "error",
        duration: 9000,
        isClosable: true,
      });
    } else {
      setMsg(false);
      const plusInfo = { plusOne, question };
      localStorage.setItem("PlusInfo", JSON.stringify(plusInfo));
      navigate("/returningGuest/laptop");
    }
  };
  useEffect(() => {
    if (!returning) {
      navigate("/returningGuest/userInfo");
    }
  }, [returning, navigate]);
  return (
    <div>
      <Badge />
      <Navigation />
      <div className={styles.contentx}>
        <div className={styles.formPadding}>
          <h3>Visitor Group</h3>
          <Slider size="35" />
          <form onSubmit={submitHandler}>
            {msg && (
              <Alert status="warning">
                <AlertIcon />
                All Fields are required!
              </Alert>
            )}
            <div className={styles.inputContainer}>
              <label>Have a visitor group?</label>
              <select
                type="text"
                className="form-control"
                onChange={questionHandler}
                placeholder="Will you be coming with a laptop"
              >
                <option value="">{data ? data.question : "Select"}</option>
                <option value="Yes">Yes</option>
                <option value="No">No</option>
              </select>
            </div>
            {question === "Yes" && (
              <>
                <div className={styles.inputContainer}>
                  <label>Enter name(s)</label>
                  <textarea
                    type="text"
                    className="form-control"
                    onChange={(e) => setPlusOne(e.target.value)}
                    placeholder="Additional reservations"
                    value={plusOne}
                  ></textarea>
                </div>
              </>
            )}

            <div className={styles.inputContainer}>
              <div className={styles.left}>
                <Link
                  to="/returningGuest/userInfo"
                  className={`${styles.customBtn} ${styles.white}`}
                >
                  <FiArrowLeftCircle /> Back
                </Link>
              </div>
              <div className={styles.right}>
                <button
                  type="submit"
                  className={`${styles.customBtn} ${styles.green}`}
                  value="Next"
                >
                  <FiArrowRightCircle /> Next
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
};

export default RPlusOne;
