import React, { useEffect } from "react";
import Navigation from "../../components/Navigation";
import styles from "../styles.module.css";
import { useNavigate } from "react-router-dom";
import Slider from "../../components/Slider";
import { useSelector, useDispatch } from "react-redux";
import {
  Alert,
  AlertIcon,
  Center,
  CircularProgress,
  Box,
  useToast,
} from "@chakra-ui/react";
import { prebookNotifyHost } from "../../redux/actions/prebookActions";
import { P_NOTIFY_RESET } from "../../redux/constants/prebookConstants";
import Badge from "../../components/Badge";

const PrebookedInfo = () => {
  let navigate = useNavigate();
  let toast = useToast();

  const sbook = useSelector((state) => state.sbook);
  const { loading, error, success, booked } = sbook;
  const pNotify = useSelector((state) => state.pNotify);
  const { loading: lNotify, error: eNotify, success: sNotify } = pNotify;

  useEffect(() => {
    if (!success) {
      navigate("/prebooked");
    }
  }, [navigate, success]);

  const dispatch = useDispatch();
  const notify = () => {
    const id = booked && booked._id;
    dispatch(prebookNotifyHost(id));
  };

  if (sNotify) {
    toast({
      title: "Notification",
      description: "Host has Notified",
      status: "success",
      duration: 9000,
      isClosable: true,
    });
    dispatch({ type: P_NOTIFY_RESET });
    navigate("/");
  }

  return (
    <div>
      <Badge />
      <Navigation />
      <div className={styles.contentx}>
        <div className={styles.formPadding}>
          <h3>Welcome {booked && booked.fullname}</h3>
          {loading ? (
            <>
              <Center>
                <CircularProgress isIndeterminate color="green.300" />
              </Center>
            </>
          ) : (
            <>
              <Slider size="95" />
              {error && (
                <Alert status="error">
                  <AlertIcon />
                  {error}
                </Alert>
              )}
              {eNotify && (
                <Alert status="error">
                  <AlertIcon />
                  {eNotify}
                </Alert>
              )}
              <Box className={styles.box}>
                <p>
                  <b>Name: </b> {booked && booked.fullname}
                </p>
                <p>
                  <b>Company: </b> {booked && booked.company}
                </p>
                <p>
                  <b>Email: </b> {booked && booked.email}
                </p>
                <p>
                  <b>Mobile: </b> {booked && booked.mobile}
                </p>
                <br />
                {lNotify ? (
                  <>
                    <Center>
                      <CircularProgress isIndeterminate color="green.300" />
                    </Center>
                  </>
                ) : (
                  <button
                    type="button"
                    className={`${styles.customBtn} ${styles.green}`}
                    onClick={notify}
                  >
                    Notify Host
                  </button>
                )}
              </Box>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

export default PrebookedInfo;
